.hi {
  width: 50px;
  height: 72px;
  background-image: url("./../sprite/hello.png");
  -webkit-animation: play .8s steps(10) infinite;
          animation: play .8s steps(10) infinite;
  margin: auto;
}

@-webkit-keyframes play {
  from { background-position:    0px; }
  to { background-position: -500px; }
}

@keyframes play {
  from { background-position:    0px; }
  to { background-position: -500px; }
}


.gif-container img{
  position: relative;
  width: 270px;
  height: 210px;
}



